.modal-catalog {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  overflow: auto;
}
.modal-catalog--hidden {
  display: none;
}
.modal-catalog__inner {
  width: 320px;
  min-height: 336px;
  box-sizing: border-box;
  margin: auto;
  padding: 28px 30px 46px;
  background-color: #ffffff;
  border-top: 1px solid #c6c6c6;
  border-bottom: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
}
@media (min-width: 768px) {
  .modal-catalog__inner {
    width: 568px;
    padding: 37px 92px 46px;
    border: 1px solid #c6c6c6;
  }
}
.modal-catalog__title {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: "Open Sans", Arial, sans-serif;
  font-style: normal;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal-catalog__title br {
    display: none;
  }
  .modal-catalog__title {
    margin-bottom: 13px;
    font-family: "Open Sans", Arial, sans-serif;
    font-style: normal;
    font-size: 30px;
    line-height: 57px;
    font-weight: 700;
    color: #000000;
  }
}
.modal-catalog__form {
  padding: 0 16px;
}
.modal-catalog__fieldset {
  margin: 0;
  margin-bottom: 31px;
  padding: 0;
  border: none;
}
.modal-catalog__legend {
  margin-bottom: 21px;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-style: normal;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  color: #444444;
  text-align: center;
}
@media (min-width: 768px) {
  .modal-catalog__legend {
    margin-bottom: 18px;
    font-family: "Open Sans", Arial, sans-serif;
    font-style: normal;
    font-size: 17px;
    line-height: 30px;
    font-weight: 400;
    color: #444444;
  }
}
.modal-catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.modal-catalog__button {
  padding: 18px 0;
  width: 100%;
}
.modal-catalog__button:not(:last-child),
.modal-catalog__list:not(:last-child) {
  margin-bottom: 10px;
}
.modal-catalog__button:disabled {
  opacity: 0.2;
  cursor: default;
}
.size-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: dense;
  column-gap: 7px;
  row-gap: 10px;
}
.size-list__control input[type="checkbox"]:hover + .size-list__marker {
  opacity: 0.8;
}
.size-list__control input[type="checkbox"]:active + .size-list__marker {
  opacity: 0.5;
}
.size-list__control input[type="checkbox"]:focus-visible + .size-list__marker {
  border: 2px solid #46c1ae;
}
.size-list__control input[type="checkbox"]:disabled + .size-list__marker {
  border: none;
  opacity: 0.2;
  cursor: default;
}
input[type="checkbox"]:checked + .size-list__marker {
  border: 2px solid #63d1bb;
}
.size-list__marker {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 82px;
  min-height: 52px;
  box-sizing: border-box;
  font-family: "Open Sans", Arial, sans-serif;
  font-style: normal;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  color: #444444;
  word-break: break-word;
  border: 2px solid rgba(68, 68, 68, 0.1);
  text-transform: uppercase;
  cursor: pointer;
}
@media (min-width: 768px) {
  .size-list__marker {
    width: 112px;
  }
}
